.gpt3__features{

    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 20px;
}
.gpt3__features-heading{
    flex:1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align:left  ;
   
}

.gpt3__features-heading h1{
    text-align: center;
    font-size: 67px;
    line-height:90px;
    font-weight: 800;
    font-family: var(--font-family);
}

.gpt3__features-heading p{
    color: var(--color-subtext);
    font-size: 30px;
    line-height: 30px;
    font-weight: 500;
    font-family: var(--font-family);

    margin-top: 2rem;

    --x: 50%;
	--y: 50%;
	
	position: relative;
	appearance: none;
	padding: 1em 2em;
	color: white;
	
	outline: none;
	border-radius: 100px;
	
	
	border: 2px solid transparent;
	background: linear-gradient(#000, #000) padding-box, radial-gradient(farthest-corner at var(--x) var(--y), #00C9A7, #845EC2) border-box;

}

.gpt3__features-container{
    margin-top: 40px;
    flex: 1.5;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.gpt3__features-heading span{
    color: wheat;
    text-decoration: underline;
    text-decoration-color: white;
   
}
.h_div{
display: inline;
}


