.gpt3__footer{
  margin-top: 20px;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.gpt3__footer-heading{
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;

}
.gpt3__footer-heading h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 64px;
    line-height: 90px;
    text-align: center;
    margin-top: 30px;

}


    .gpt3__footer-btn {
        display: flex;
        justify-content: center;
        align-items: center;
    
        padding: 1rem;
        border: 1px solid #fff;
        text-align: center;
    
        margin-bottom: 10rem;
        cursor: pointer;
    }

     

.gpt3__footer-btn p{
   

    font-size: 18px;
    font-family: var(--font-family);
    line-height: 21px;
    color: #fff;
    word-spacing: 2px;
     
}


.gpt3__footer-links{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    text-align: left;
    
}

.gpt3__footer-links div{
    width: 250px;
    margin: 1rem;

}

.gpt3__footer-links_logo {
    align-items: normal;
    display: flex;
    flex-direction: column;
}

.gpt3__footer-links_logo img {
    width: 100px;
    height: 40px;
    margin-bottom: 1rem;
}

.gpt3__footer-links_logo p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 15px;
    color: #fff;
}

.gpt3__footer-links_div {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;

}

.gpt3__footer-links_div h4{
    font-size: 14px;
    line-height: 17px;
    font-family: var(--font-family);
    color: #fff;
    margin-bottom:1rem ;

}
.gpt3__footer-links_div p{
    cursor: pointer;
    font-size: 12px;
    line-height: 14px;
    font-family: var(--font-family);
    color: #fff;
    margin: 0.5rem 0;

}

.gpt3__footer-copyright{
    margin-top: 2rem;
    font-family: var(--font-family);
    width: 100%;
    text-align: center;
}
.gpt3__footer-copyright{
    font-size: 12px;
    font-family: var(--font-family);
    line-height: 15px;
    color: #fff;
}











@media screen and (max-width: 850px) {
    .gpt3__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

    .gpt3__footer-links div {
        margin: 1rem 0;
    }

    .gpt3__footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 400px) {
    .gpt3__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }
}




/* button */
.waiting{
    --x: 20%;
	--y: 20%;
	font-size: 40px;
	position: relative;
	appearance: none;
    font-weight: 600;
	padding: 0.4rem 1rem;
	color: white;
    font-family: var(--font-family);
	margin-top: 30px;
	outline: none;
	border-radius: 10px;
	
	
	border: 2px solid transparent;
	background: linear-gradient(#000, #000) padding-box, radial-gradient(farthest-corner at var(--x) var(--y), #00C9A7, #845EC2) border-box;
}




hr{
    
    width: 100vw;
}