.gpt3__navbar{
overflow-x: hidden;
width: 100vw;
 z-index:999;
 display:flex;
 justify-content: space-between;
 align-items: center;
 padding: 1.6rem 6rem;
 box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.gpt3__navbar-links{
    flex:1;
    display:flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}


.gpt3__navbar-links_logo {
 display: flex;
     
}
.gpt3__navbar-links_logo img{
  align-items: center;
  justify-content: center;
     
}

.gpt3__navbar-links_container{
    display: flex;
    flex-direction: row ;
    margin-right: 18px;
}
.gpt3__navbar-sign{
    display:flex;
    justify-content: flex-end;
    align-items: center;
}

.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p
{
    color: #fff ;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 20px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 0.5rem;
    cursor:pointer;
}   


.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button{
    padding: 0.5rem 1rem;
    color: #fff;
    /* left:1153px;
    top:55px; */
    font-family: var(--font-family);
    background: rgb(255, 102, 0) ;
    font-style: normal;
    font-weight: 500;
    font-size:18px;
    line-height: 25px;
    cursor: pointer;
    border-radius: 5px;
    border: 0;
    outline: none;
    /* border: 0;
    outline: none; */

}


/* menu */

.gpt3__navbar-menu{
    margin-left: 1rem;
    display:none;
    position: relative;

}

.gpt3__navbar-menu svg{
    cursor: pointer;
    
}

.gpt3__navbar-menu_container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction:column;
    text-align: end;
    background-color: var(--color-footer);
    padding:2rem;
    position:absolute;
    top:40px;
    right:0;
    margin-top: 1rem;
    min-width:210px;
    border-radius: 5px;
    box-shadow: 0 0 5(0,0,0,0,.2);

}


.gpt3__navbar-menu_container p{
margin:1rem 0;

}

.gpt3__navbar-menu_container-links-sign{
    display: none;
}

/*  */

.wrapper {
    display: flex;
    justify-content: center;
  }
  
  .cta {
      display: flex;
      padding: 6px 30px;
      text-decoration: none;
      font-family:'Manrope', sans-serif;
      font-weight: 700;
      font-size: 20px;
      color: white;
      background: #6225E6;
      transition: 1s;
      box-shadow: 6px 6px 0 black;
      transform: skewX(-15deg);
  }
  
  .cta:focus {
     outline: none; 
  }
  
  .cta:hover {
      transition: 0.5s;
      box-shadow: 10px 10px 0 #f37932;
  }
  
  .cta span:nth-child(2) {
      transition: 0.5s;
      margin-right: 0px;
  }
  
  .cta:hover  span:nth-child(2) {
      transition: 0.5s;
      margin-right: 45px;
  }
  
    span {
      transform: skewX(15deg) 
    }
  
    span:nth-child(2) {
    
      width: 10px;
      margin-left: 10px;
      position: relative;
      top: 12%;
    }
    
  /**************SVG****************/
  
  path.one {
      transition: 0.4s;
      transform: translateX(-60%);
  }
  
  path.two {
      transition: 0.5s;
      transform: translateX(-30%);
  }
  
  .cta:hover path.three {
      animation: color_anim 1s infinite 0.2s;
  }
  
  .cta:hover path.one {
      transform: translateX(0%);
      animation: color_anim 1s infinite 0.6s;
  }
  
  .cta:hover path.two {
      transform: translateX(0%);
      animation: color_anim 1s infinite 0.4s;
  }
  
  /* SVG animations */
  
  @keyframes color_anim {
      0% {
          fill: white;
      }
      50% {
          fill: #FBC638;
      }
      100% {
          fill: white;
      }
  }

/*  */


@media screen and (max-width: 1100px){
    .gpt3__navbar-links_container{
        display:none;
    }
    .gpt3__navbar-menu{
        display: flex;
    }

}
    
@media screen and (max-width: 750px){
    .gpt3__navbar{
        padding: 2rem 4rem;
    }

}
    
@media screen and (max-width: 550px){
    .gpt3__navbar{
        padding: 2rem ;
    }

    .gpt3__navbar-sign{
        display: none;
    }

    .gpt3__navbar-menu_container{
        top: 20px;
        /* top: 20px; */
    }
.gpt3__navbar-menu_container-links-sign{
    display: block;
}
}


    
