.gpt3__possibility{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;


}

.gpt3__possibility-image{
  
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 2rem;
    margin-bottom: 70px;
}


.gpt3__possibility-image img{
    width: 100%;
    height: 100%;

}

.gpt3__possibility-content{
    margin-top: 70px;
    flex:1;
     display: flex;
     flex-direction: column;
     justify-content: flex-end;
     align-items: flex-start;
     position: relative;
     /* background-image: url("../../assets/desk.jpg");
     background-size: cover; */;
     box-shadow: 0px 0px 0px #000000 inset,-50px -50px 110px #030303 inset;
     padding: 4rem;
        color: white;
        border-width: 3px;
        border-style: solid;
        border-image: 
          linear-gradient(
            to bottom, 
            rgb(140, 0, 255), 
            rgba(0, 0, 0, 0)
          ) 1 100%;
}


.gpt3__possibility-content:before {
    content: ' ';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    opacity: 0.1;
    background-image: url("../../assets/desk.jpg");
    background-repeat: no-repeat;
    background-position: 50% 0;
    background-size: cover;
    
  }







.gpt3__possibility-content h4 {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71E5FF;
    
}

.gpt3__possibility-content h4:last-child {
    color: #ff8A71;
    font-size: 20px;
    
}
.gpt3__possibility-content h4:first-child {
    color: #ff8A71;
    font-size: 50px;
    
}

.gpt3__possibility-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 70px;
    line-height: 90px;
    margin: 1rem 0;
}

.gpt3__possibility-content p {
    color: var(--color-text);
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;

    margin-bottom: 2rem;
}




@media screen and (max-width: 950px) {
    .gpt3__possibility {
        flex-direction: column;
    }

    .gpt3__possibility-image {
        margin: 1rem 0;
        
    }

    .gpt3__possibility-image img {
        width: 100%;
        height: 100%;
    }

    .gpt3__possibility-content {
        margin-top: 2rem;
    }
}

@media screen and (max-width: 700px) {
    .gpt3__possibility-image img {
        width: 100%;
        height: 100%;
    }
}













/* blockquote */
.arrow-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-bottom: 30px;
    padding-bottom: 20px;
    
  }
  
  .chevron {
    position: absolute;
    width: 2.1rem;
    height: 0.48rem;
    opacity: 0;
    transform: scale(0.3);
    -webkit-animation: move-chevron 3s ease-out infinite;
            animation: move-chevron 3s ease-out infinite;
  }
  
  .chevron:first-child {
    -webkit-animation: move-chevron 3s ease-out 1s infinite;
            animation: move-chevron 3s ease-out 1s infinite;
  }
  
  .chevron:nth-child(2) {
    -webkit-animation: move-chevron 3s ease-out 2s infinite;
            animation: move-chevron 3s ease-out 2s infinite;
  }
  
  .chevron:before,
  .chevron:after {
    content: "";
    position: absolute;
    top: 0;
    height: 100%;
    width: 50%;
    background: #f4f6f8;
  }
  
  .chevron:before {
    left: 0;
    transform: skewY(30deg);
  }
  
  .chevron:after {
    right: 0;
    width: 50%;
    transform: skewY(-30deg);
  }
  
  @-webkit-keyframes move-chevron {
    25% {
      opacity: 1;
    }
    33.3% {
      opacity: 1;
      transform: translateY(2.28rem);
    }
    66.6% {
      opacity: 1;
      transform: translateY(3.12rem);
    }
    100% {
      opacity: 0;
      transform: translateY(4.8rem) scale(0.5);
    }
  }
  
  @keyframes move-chevron {
    25% {
      opacity: 1;
    }
    33.3% {
      opacity: 1;
      transform: translateY(2.28rem);
    }
    66.6% {
      opacity: 1;
      transform: translateY(3.12rem);
    }
    100% {
      opacity: 0;
      transform: translateY(4.8rem) scale(0.5);
    }
  }




  .arrow2{
    margin-top: 30px;
    margin-bottom: 10px;
  }