.gpt3__header {
    display: flex;
    flex-direction: column;
    /* background-image: url('../../assets/backheader.png'); */
    background-image: url('../../assets/sharpened2.png');
    /* background-image: url('../../assets/sharpened.jpg'); */
    /* background-image: url('../../assets/bluegraph.jpg'); */
    background-size: 1850px;
    
    /* background-size: 1650px; */
    /* background-image: url('../../assets/bluebg2.png'); */
    justify-content: center;
    align-items: center;
    width: 100vw;
    padding: 1rem 1rem;
    padding-top: 30px;
    /* box-shadow: rgba(50, 50, 93, 0.4) 0px 0px 890px -12px inset, rgba(0, 0, 0, 0.5) 800px 1900px 890px -18px inset; */
    
    /* box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px; */
   
    
}

.bordered{
    --x: 50%;
	--y: 50%;
	
	position: relative;
	appearance: none;
	padding: 1em 2em;
	color: white;
	
	outline: none;
	border-radius: 100px;
	
	
	border: 2px solid transparent;
	background: linear-gradient(#000, #000) padding-box, radial-gradient(farthest-corner at var(--x) var(--y), #00C9A7, #845EC2) border-box;
}


.unlimited{
    margin-top: 20px;
    
}
.unlimited img{
    height: 150px;
    cursor: pointer;
 
}





.gpt3__header-content {
    /* margin-top: 150px; */
   
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
}

.gpt3__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 58px;
    line-height: 60px;
    margin-bottom: 10px;
    letter-spacing: -0.04em;
}
.gpt3__header-content span {
    color: wheat;
    text-decoration: underline;
    text-decoration-color: white;
   
           
}


#appHeaderFirstText{
    font-weight: 700;
    background: linear-gradient(
        to right,
        #7953cd 20%,
        #fab700 30%,
        #0190cd 70%,
        #764ada 80%
    );
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    background-size: 500% auto;
    animation: textShine 2s ease-in infinite alternate;
   
    font-size: 40px;
    line-height: 45px;
    padding-left: 40px;
    padding-right: 40px;
    text-align: center;
   
    /* border: 1px solid #ffffff ; */
    
    
    
}




#appHeaderSecondText{
    background: linear-gradient(
        to right,
        #7953cd 20%,
        #fab700 30%,
        #0190cd 70%,
        #764ada 80%
    );
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    background-size: 500% auto;
    animation: textShine 2s ease-in infinite alternate;
    margin-bottom: 30px;
    line-height: 48px;
    font-weight: 600;
}

@keyframes textShine {
    0% {
        background-position: 0% 50%;
    }
    100% {
        background-position: 100% 50%;
    }
}


.gpt3__header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 30px;
    line-height: 28px;
    color: var(--color-text);

    margin-top: 0.5rem;
}

.gpt3__header-content__input {
    width: 100%;
    margin: 2rem 0 1rem;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.gpt3__header-content__input input {
    flex: 2;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    background: var(--color-footer);
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #fff;

    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.gpt3__header-content__input button {
   
    width: 610px;
    height: 80px;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 40px;
    line-height: 28px;
    background: rgb(255, 102, 0);
    border: none;
    padding: 0 0.5rem;
    color: #fff;
    cursor: pointer;
    outline: none;
   transition-duration: 1s;
}



.gpt3__header-content__input button:hover {
    
    background: rgb(174, 0, 255);
    
;

   
}

.gpt3__header-content__people {
    
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    margin-top: 1 rem;
    
}



.gpt3__header-content__people img {
    width: 181.79px;
    height: 38px;
}

.gpt3__header-content__people p {
    margin: 0 0 0 1rem;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 17px;
    line-height: 38px;
    color: #fff;
    text-align: center;
}



.gpt3__header-image {
   
    padding-top: 30px;
    padding-right: 65px;
    display: flex;
    justify-content: center;
    align-items: center;


}

.gpt3__header-image img {
    
    width: 100%;
    height: 100%;
    
}

.newtextt{
    display: flex;
}
.newtextt p{
    font-size: 60px;
}


iframe {
        --x: 50%;
        --y: 50%;
	
	position: relative;
	appearance: none;
	padding: 0.1rem 0.1rem;
	color: white;
	cursor: pointer;
	outline: none;
	border-radius: 50px;
	
	
	border: 4px solid transparent;
	background: linear-gradient(#000, #000) padding-box, radial-gradient(farthest-corner at var(--x) var(--y), #00C9A7, #845EC2) border-box;
}












@media screen and (max-width: 1400px) {
    .gpt3__header-image img {
        height: max-content;
    }
}





@media screen and (max-width: 1270px) {
    .gpt3__header {
        flex-direction: column;
    }

    .gpt3__header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .gpt3__header-content h1 {
        font-size: 14px;
        line-height: 60px;
    }

    .gpt3__header-content p {
        font-size: 16px;
        line-height: 24px;
    }
    
    .gpt3__header-content__people {
        flex-direction: column;
    }

    .gpt3__header-content__people p {
        margin: 0;
    }

    .gpt3__header-content__input input,
    .gpt3__header-content__input button {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .gpt3__header-content h1 {
        font-size: 12px;
        line-height: 10px;
    
    }

    .gpt3__header-content p {
        font-size: 14px;
        line-height: 24px;
    }

    .gpt3__header-content__input input,
    .gpt3__header-content__input button {
        font-size: 12px;
        line-height: 16px;
    }
}





