
.App{
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body{
  overflow-x: hidden;
    background: var(--color-bg) 
}

a{
    color: unset;
    text-decoration: none;
}

/* background */
.gradient__bg{
    
     background:-moz-radial-gradient(circle at 3% 25%, rgb(68, 0, 82,1) 0%, rgba(4, 12, 24, 1) 100%);

     /* safari 5.1+,chrome 10+ */
     background:-webkit-radial-gradient(circle at 3% 25%, rgba(68, 0, 82,1) 0%, rgba(4, 12, 24, 1) 100%);
 
     /* opera 11.10+ */
     background:-o-radial-gradient(circle at 3% 25%, rgba(68, 0, 82,1) 0%, rgba(4, 12, 24, 1) 100%);
 
     /* ie 10+ */
     background:-ms-radial-gradient(circle at 3% 25%, rgba(68, 0, 82,1) 0%, rgba(4, 12, 24, 1) 100%);
 
     /* global 92%+ browsers support */
     background:radial-gradient(circle at 3% 25%, rgba(68, 0, 82,1) 0%, rgba(4, 12, 24, 1) 100%);
}
.gradient__bg2{
    
     background:-moz-radial-gradient(circle at 3% 25%, rgb(0, 51, 77) 0%, rgb(0, 37, 32) 100%);

     /* safari 5.1+,chrome 10+ */
     background:-webkit-radial-gradient(circle at 3% 25%, rgb(0, 45, 82) 0%, rgb(0, 30, 26) 100%);
 
     /* opera 11.10+ */
     background:-o-radial-gradient(circle at 3% 25%, rgb(0, 57, 79) 0%, rgb(0, 49, 52) 100%);
 
     /* ie 10+ */
     background:-ms-radial-gradient(circle at 3% 25%, rgb(0, 56, 81) 0%, rgba(4, 12, 24, 1) 100%);
 
     /* global 92%+ browsers support */
     background:radial-gradient(circle at 3% 25%, rgb(0, 44, 65) 0%, rgba(4, 12, 24, 1) 100%);
}

/* text */

.gradient__text{
  /* font-family: var(--font-family); */
    background:var(--gradient-text);
    background-clip:text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

/* section padding */
.section__padding{
    padding: 4rem 6rem;
}
.section__margin{
    margin: 4rem 6rem;
}

/* animation */
.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}


 @-webkit-keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  /* animation end  */


  /* for tablets */
  @media screen and (max-width: 700px){
   .section__padding{
    padding: 4rem;
   }
   .section__margin{
    padding: 4rem;
   }

  }

  /* for mobiles */

  @media screen and (max-width: 550px){
   .section__padding{
    padding: 4rem 2rem;
   }
   .section__margin{
    padding: 4rem 2rem;
   }

  }
    
  