.arrows-container {
    display: flex;
    justify-content: center;
    align-items: center;    
    position: relative;
    right: 100px;
    transform: rotate(270deg);
  }
  
  .chevrono {
    position: absolute;
    width: 2.1rem;
    height: 0.48rem;
    opacity: 0;
    transform: scale(0.3);
    -webkit-animation: move-chevrono 3s ease-out infinite;
            animation: move-chevrono 3s ease-out infinite;
  }
  
  .chevrono:first-child {
    -webkit-animation: move-chevrono 3s ease-out 1s infinite;
            animation: move-chevrono 3s ease-out 1s infinite;
  }
  
  .chevrono:nth-child(2) {
    -webkit-animation: move-chevrono 3s ease-out 2s infinite;
            animation: move-chevrono 3s ease-out 2s infinite;
  }
  
  .chevrono:before,
  .chevrono:after {
    content: "";
    position: absolute;
    top: 0;
    height: 100%;
    width: 50%;
    background: #f4f6f8;
  }
  
  .chevrono:before {
    left: 0;
    transform: skewY(30deg);
  }
  
  .chevrono:after {
    right: 0;
    width: 50%;
    transform: skewY(-30deg);
  }
  
  @-webkit-keyframes move-chevrono {
    25% {
      opacity: 1;
    }
    33.3% {
      opacity: 1;
      transform: translateY(2.28rem);
    }
    66.6% {
      opacity: 1;
      transform: translateY(3.12rem);
    }
    100% {
      opacity: 0;
      transform: translateY(4.8rem) scale(0.5);
    }
  }
  
  @keyframes move-chevrono {
    25% {
      opacity: 1;
    }
    33.3% {
      opacity: 1;
      transform: translateY(2.28rem);
    }
    66.6% {
      opacity: 1;
      transform: translateY(3.12rem);
    }
    100% {
      opacity: 0;
      transform: translateY(4.8rem) scale(0.5);
    }
  }
